.canvas-size {
  position: absolute;
  bottom: 20px;
  display: flex;
  flex-direction: column;
  margin-left: 25px;
}

.canvas-size__item {
  display: flex;
  color: #dbdbdb;
  font-size: 16px;
  margin-bottom: 10px;
}

.canvas-size__item input {
  margin-right: 5px;
  width: 15px;
  height: 15px;
}

.canvas-size__item label {
  align-self: center;
}
