.result-export__wrapper {
  position: absolute;
  top: 5px;
  right: 0;
}

.result-export__button {
  width: 55px;
  height: 55px;
  padding: 0;
  border: 2px solid #444;
  border-radius: 20%;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  background-image: url("./img/export.png");
}

.result-export__list {
  display: none;
  flex-direction: column;
  width: 55px;
}

.result-export__list--active {
  display: flex;
  right: 0;
  margin-top: 5px;
}

.result-export__item {
  width: 55px;
  height: 55px;
  padding: 0;
  border: 2px solid #444;
  border-radius: 20%;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  margin-bottom: 5px;
  background-size: 45px;
  background-repeat: no-repeat;
  background-position: center;
}

.result-export__button--active,
.result-export__button:hover,
.result-export__item--active,
.result-export__item:hover {
  border: 2px solid gold;
}

.png {
  background-image: url("./img/png.png");
}

.set-png {
  background-image: url("./img/png-set.png");
}

.gif {
  background-image: url("./img/gif.png");
}

.apng {
  background-image: url("./img/apng.png");
}

.save {
  background-image: url("./img/save.png");
}

.reset {
  background-image: url("./img/reset.png");
}
