.pen-size {
  position: absolute;
  top: 50px;
  left: 50px;
  display: flex;
}

.pen-size__item {
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  margin-right: 2px;
  border-style: solid;
  border-width: 2px;
  border-color: #444;
  cursor: pointer;
}

.pen-size__item--active {
  border: 2px solid gold;
}

.pen-size__item div {
  background: #dbdbdb;
  box-sizing: border-box;
  margin: auto;
  width: 16px;
  height: 16px;
}

.pen-size__itemS div {
  border: 5.2px solid black;
}

.pen-size__itemM div {
  border: 4px solid black;
}

.pen-size__itemL div {
  border: 2px solid black;
}

.pen-size__itemXL div {
  border: 1px solid black;
}
