.tools-wrapper {
  display: inline-flex;
  margin: 80px 0 0 35px;
  flex-direction: column;
}

.tools {
  position: relative;
  display: inline-flex;
  flex-direction: column;
}

.tools__item {
  display: inline-flex;
  list-style: none;
  margin-bottom: 3px;
  width: 55px;
  height: 55px;
}

.tools__item:first-child {
  margin-right: 5px;
}

.tools__item button {
  background-color: #212121;
  width: 55px;
  height: 55px;
  padding: 0;
  border: 2px solid #444;
  border-radius: 20%;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}

.tools__item:hover button {
  background-color: #313131;
}

.fill-color-action {
  background-image: url("./img/fillColor.png");
  background-size: 40px;
}

.full-fill-color-action {
  background-image: url("./img/fullFillColor.png");
  background-size: 40px;
}

.draw-action {
  background: url("./img/pencil.png");
}

.dithering-action {
  background: url("./img/dithering.png");
  background-size: 35px;
}

.pen-action {
  background-image: url("./img/pen.png");
  background-size: 40px;
}

.choose-color-action {
  background: url("./img/chooseColor.png");
}

.erase-action {
  background: url("./img/eraser.png");
}

.stroke-action {
  background: url("./img/stroke.png");
  background-size: 40px;
}

.circle-action {
  background: url("./img/ellipse.png");
  background-size: 40px;
}

.rectangle-action {
  background: url("./img/rectangle.png");
  background-size: 40px;
}

.fill-rectangle-action {
  background: url("./img/fillRectangle.png");
  background-size: 40px;
}

.mirror-action {
  background: url("./img/mirror.png");
  background-size: 40px;
}

.lighten-action {
  background: url("./img/lighter.png");
  background-size: 40px;
}

.darken-action {
  background: url("./img/darken.png");
  background-size: 30px;
}

.move-action {
  background: url("./img/move.png");
  background-size: 40px;
}

.fill-pixels-same-color-action {
  background: url("./img/allPixelsAtTheSameColor.png");
  background-size: 40px;
}

.tools__item--active button {
  border: 2px solid gold;
  border-radius: 20%;
}

.color-palette-wrapper {
  display: flex;
  position: relative;
  width: 90px;
  height: 90px;
}

.color-palette__primary input,
.color-palette__secondary input {
  height: 59px;
  box-sizing: border-box;
  overflow: hidden;
  width: 59px;
}

.color-palette__primary {
  top: 0;
  left: 0;
  position: absolute;
  z-index: 1;
}

.color-palette__secondary {
  top: 35px;
  right: 0;
  position: absolute;
  z-index: 0;
}

.color-change {
  width: 24px;
  height: 24px;
  position: absolute;
  bottom: 6px;
  left: 6px;
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('./img/colorChange.png');
  transition: transform 200ms ease-in-out;
  transform: rotate(-10deg);
}

.color-change:hover {
  transform: rotate(-190deg);
}

