.frame {
  width: 110px;
  height: 110px;
  box-sizing: border-box;
  position: relative;
  background-image: url("../app/img/canvas-background-light.png");
  margin-bottom: 7px;
  border-radius: 5%;
  border: 2px solid transparent;
  user-select: none;
}

.frame button {
  position: absolute;
  width: 28px;
  height: 28px;
  padding: 0;
  display: none;
  border: none;
  cursor: pointer;
  background-color: #dbdbdb;
  outline: none;
}

.frame .add-to-animation-action {
  display: block;
  background-color: gold;
  border-bottom-right-radius: 5px;
  border-top-left-radius: 5px;
  font-size: 20px;
}

.frame:hover button {
  display: block;
}

.frame:hover .add-to-animation-action,
.frame--active .add-to-animation-action {
  background-color: gold;
}

.frame:hover .add-to-animation-action--inactive,
.frame--active .add-to-animation-action--inactive {
  background-color: #444444;
}

.frame .add-to-animation-action--inactive {
  background-color: #444444;
}

.frame:hover,
.frame--active {
  border: 2px solid gold;
}

.frame__img {
  width: 105px;
  height: 105px;
  pointer-events: none;
  z-index: 100;
}

.delete-action {
  top: 0;
  right: 0;
  background: url("./img/trash.png") no-repeat center;
}

.delete-action--hidden {
  visibility: hidden;
}

.copy-action {
  bottom: 0;
  right: 0;
  background: url("./img/copy.png") no-repeat center;
}

.move-action {
  bottom: 0;
  left: 0;
}

.add-to-animation-action {
  top: 0;
  left: 0;
}

.frame--moving {
  border: 3px dashed gold;
  background-color: #444444;
}

.frame--moving button {
  visibility: hidden;
}

