.container {
  display: block;
  background-color: #333333;
}

.screenshot__wrapper {
  margin: 0 auto;
  width: 1000px;
  height: 600px;
  position: relative;
}

.screenshot__description {
  color: #dbdbdb;
  font-size: 30px;
  margin-top: 30px;
  padding-left: 30px;
}

.screenshot {
  width: 1000px;
  height: 500px;
}

.link-to-editor {
  background-color: gold;
  width: 150px;
  height: 50px;
  padding: 0;
  border: 2px solid #212121;
  border-radius: 5%;
  color: #212121;
  box-sizing: border-box;
  outline: none;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  bottom: 30px;
  right: 40px;
}

.link-to-editor a {
  color: #212121;
  font-size: 24px;
  text-decoration: none;
}

.animation__wrapper {
  width: 1250px;
  margin: 30px auto;
}

.animation__example-wrapper {
  display: flex;
}

.animation__title {
  color: #dbdbdb;
  font-size: 20px;

}

.animation__example {
  border: 5px solid #212121;
  border-radius: 1%;
  margin-right: 10px;
  height: 400px;
}

.animation__example img {
  width: 400px;
  height: 400px;
}

.animation__title {
  text-align: center;
  margin-bottom: 20px;
}

.functionality {
  display: flex;
  width: 850px;
  margin: 50px auto;
}

.functionality li {
  list-style: circle;
  margin-bottom: 8px;
}

.functionality li:first-child {
  margin-top: 20px;
}

.functionality__list {
  width: 400px;
  color: #dbdbdb;
  font-size: 20px;
}

.functionality__list:first-child {
  margin-right: 50px;
}

li.functionality__header {
  list-style: none;
  margin-top: 20px;
  padding-bottom: 5px;
  text-align: center;
}

.functionality__header > span {
  border-bottom: 1px solid #dbdbdb;
}

.author__info {
  background-color: #333333;
  color: #dbdbdb;
  font-size: 30px;
  padding-bottom: 40px;
  padding-left: 20px;
}

.author__info a {
  text-decoration: none;
  color: #dbdbdb;
  font-weight: bold;
}
