.keyboard-shortcuts__wrapper {
  position: absolute;
  bottom: 20px;
  left: 60px;
  width: 48px;
  height: 25px;
}

.keyboard-shortcuts__button {
  background-image: url("./img/keyboard.png");
  padding: 0;
  width: 48px;
  height: 25px;
  border: 2px solid #444;
  border-radius: 10%;
  color: inherit;
  box-sizing: border-box;
  outline: none;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}

.keyboard-shortcuts__button:hover,
.keyboard-shortcuts__button--active {
  border: 2px solid gold;
}

.keyboard-shortcuts__list {
  background-color: #333333;
  width: 800px;
  height: 680px;
  display: none;
  position: absolute;
  top: -660px;
  left: 50px;
  z-index: 20;
  border-radius: 5px;
}

.keyboard-shortcuts__list--active {
  display: flex;
}

.keyboard-shortcuts li:first-child {
  text-align: center;
  font-size: 24px;
  color: #dbdbdb;
  padding-top: 10px;
  padding-bottom: 10px;
}

.keyboard-shortcuts {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.keyboard-shortcuts__item {
  margin-top: 5px;
  display: flex;
  width: 300px;
  align-items: center;
}

.item__image {
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: center;
  width: 30px;
  height: 30px;
}

.item__key-code,
.item__image {
  border: 2px solid gold;
  border-radius: 20%;
  text-align: center;
  margin-right: 10px;
  outline: none;
}

.item__key-code {
  font-size: 20px;
  width: 34px;
  height: 34px;
  background-color: #333333;
}

.item__description {
  font-size: 20px;
  color: gold;
  margin-right: 10px;
}

.draw-action {
  background-image: url("../tools/img/pencil.png");
}

.erase-action {
  background-image: url("../tools/img/eraser.png");
}

.mirror-draw-action {
  background-image: url("../tools/img/mirror.png");
}

.fill-color-action {
  background-image: url("../tools/img/fillColor.png");
}

.full-fill-color-action {
  background-image: url("../tools/img/fullFillColor.png");
}

.magic-color-bucket-action {
  background-image: url("../tools/img/allPixelsAtTheSameColor.png");
}

.fill-rectangle-action {
  background-image: url("../tools/img/fillRectangle.png");
}

.stroke-action {
  background-image: url("../tools/img/stroke.png");
}

.rectangle-action {
  background-image: url("../tools/img/rectangle.png");
}

.circle-action {
  background-image: url("../tools/img/circle.png");
}

.move-action {
  background-image: url("../tools/img/move.png");
}

.lighten-action {
  background-image: url("../tools/img/lighter.png");
}

.darken-action {
  background-image: url("../tools/img/darken.png");
}

.dithering-action {
  background-image: url("../tools/img/dithering.png");
}

.choose-color-action {
  background-image: url("../tools/img/chooseColor.png");
}

.pen-action {
  background-image: url("../tools/img/pen.png");
}

.arrow-up {
  background-image: url("./img/arrowUp.png");
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: center;
}

.arrow-down {
  background-image: url("./img/arrowDown.png");
  background-size: 25px;
  background-repeat: no-repeat;
  background-position: center;
}

.next-block {
  margin-top: 20px;
}

.keys-combination {
  width: 70px;
  border-radius: 10%;
}

.close-window {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 0;
  width: 20px;
  height: 20px;
  border: none;
  background-color: #333333;
  font-size: 20px;
  outline: none;
}


