.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.link-to-landing-page {
  color: #dbdbdb;
  text-decoration: none;
}

header {
  height: 50px;
  background-color: #333;
  border-bottom: 2px solid #dbdbdb;
  text-align: center;
  font-size: 35px;
  color: #dbdbdb;
  line-height: 50px;
  box-sizing: border-box;
}

main {
  display: flex;
  width: 100%;
  height: calc(100% - 50px);
}

.content {
  width: 640px;
  position: relative;
  display: flex;
  background-image: url("img/canvas-background-medium.png");
  background-position-y: 6px;
}

.low-canvas {
  position: absolute;
  top: 31px;
  left: 0;
  z-index: 0;
}

.low-canvas--active {
  z-index: 20;
}

.top-canvas {
  align-self: center;
  position: relative;
  z-index: 10;
  background-image: url("img/canvas-background-light.png");
}

.sidebar-left,
.sidebar-right {
  width: 450px;
  background-color: #212121;
  position: relative;
}

.sidebar-left{
  display: flex;
}

